import React, {useState, useEffect} from 'react';
import '../../Assets/CSS/Home.css';
import PostMasonry from './moment/PostMasonry';
import PostMasonry2 from './moment/PostMasonry2';
import trending from '../Data/trending';
import featured from '../Data/featured';
import Gallery from './NFCSGallery/Gallery';
import Footer from '../Footer/Footer';
import Category from './CategoryHeader';
import axios from 'axios'
import { Button } from '@mui/material';
const BASE_URL = 'http://127.0.0.1:8000';

const featuredConfig = {
 0: {
    gridArea: '1 / 1 / 2 / 3',
    height: '300px',
 },
 1: {
    height: '220px',
 },
 3: {
    
 }
}
const trendingConfig = {
    1: {
        gridArea: '1 / 2 / 3 / 3'
    }
}
const lastFeatured = featured.slice(-1)[0]
const mergeStyle = function (posts, config){
    posts.forEach((post, index) => {
        post.style = config[index]
    })
}



function Home() {
const bg = {
    backgroundColor: 'red',
}
const windowWidth = window.innerWidth 
const pixel = 2;

//States
const [featuredPost, setFeaturedPost] = useState([])
const [blogPost, setBlogPost] = useState([])

useEffect(() => {
    axios.get(`${BASE_URL}/blog/featured/`)
    .then(res => {
        setFeaturedPost(res.data)
    })
    .catch(error => {
        console.log(error)
    })

},[])

useEffect(() => {
       axios.get(`${BASE_URL}/blog/`)
       .then(res => {
        setBlogPost(res.data) 
       })
       .catch((error) => {
        //Errors here
       })
},[])
const blogPosts = blogPost.slice(2)
console.log("sliced ", blogPosts)
mergeStyle(blogPosts, trendingConfig)
mergeStyle(featuredPost, featuredConfig)
//console.log('featured post '. featuredPost)
console.log("posts here, yea ", blogPost)
const fontStyle = {color: 'red'}
    return(
        <>
        
        <section className='container home' style={{}}>
            <Category />
            <div className='row'>
                <h2>Featured Post</h2>
                <section className="featured__post">   
                    
                    <PostMasonry posts={featuredPost} columns={2} />
                    {/* <PostMasonry2 post={featured} tagsOnTop={true} /> */} 
                </section>
                <span className='view_all'>
                    <Button>View All</Button>
                </span>

                <h2>Trending Posts</h2>
                <PostMasonry posts={blogPosts} columns={3} fontStyle={fontStyle} />
                <span className='view_all'>
                    <Button >View All</Button>
                </span>

            <h2 className="nfcs__gallery" style={{marginBottom: '2rem', textAlign:'center'}}>NFCS Gallery</h2>
            <section className='nfcs__gallery'>
                <Gallery  background={bg} windowWidth={windowWidth} pixel={pixel} trend={trending} />
            </section>
            </div>
        </section>
        <Footer />
        </>
        )
    }
export default Home;