import React, {useState} from 'react'
import { Button } from '@mui/material';
import { Link,useNavigate} from 'react-router-dom';
import {connect} from 'react-redux';
import '../../../Assets/CSS/requests.css';
import SendIcon from '@mui/icons-material/Send';

import { reset_password_confirm } from '../../Actions/auth';

function PasswordReset({match, reset_password_confirm}) { 
   const navigate = useNavigate()
   const [disable, setDisable] = useState(false)
   const [processing, setProcessing] = useState(false)
   const [matchErr, setMatchErr] = useState("")
   const [longErr, setIsLong] = useState("") 

  const [formData, setFormData] = useState({
    "new_password": "",
    "re_new_password": ""
  })

  const {email} = formData; // Destructure the state

  
  const handleChange = (e) => {
    setFormData({...formData, [e.target.name]:e.target.value}) 
  }
  
  const {new_password, re_new_password} = formData;

  const handleSubmit = (e) => {
    e.preventDefault();
    const uid = match.params.uid;
    const token = match.params.token;

        if(new_password !== re_new_password){
            setMatchErr("Passwords do not match!") 
        }else{
            setMatchErr("")
        }
        if(new_password.length < 8 && re_new_password !== ""){
            setIsLong("Password should be at least 8 characters")
          } else(
            setIsLong("")
          )


    reset_password_confirm(uid, token, new_password, re_new_password)
  setDisable(true)
  setProcessing(true)
  }

  return (
    <div className='Requests'>
    <div className='cont'>
        <h3>Password Reset</h3>
        <div className='reset_card'>
            <form onSubmit={e => handleSubmit(e)}>

                <p>
                    Please enter your new password 
                </p>

                   <input onChange={(e) => handleChange(e) } type='password' placeholder='New password' autoFocus={true} required
                  value={new_password}
                  name='new_password'
                />
                   <input onChange={(e) => handleChange(e) } type='password' placeholder='Confirm new password' required
                  value={re_new_password}
                  name='re_new_password'
                /> 
                {matchErr ? <code>{matchErr}</code> : "" }
                {longErr ? <code>{longErr}</code> : "" }
                <Button disable={disable && processing}
                    type="submit" className='login__button'>
                        {processing ? "Sending..." :"Reset Password"}
                </Button>
                
                
            </form>
        </div>
      </div>
    </div>
    
  )
}
export default connect(null, {reset_password_confirm}) (PasswordReset);