import react, {useState} from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Avatar } from '@mui/material';
import '../../Assets/CSS/NavBar.css';
import DensitySmallSharpIcon from '@mui/icons-material/DensitySmallSharp';
import { connect } from 'react-redux';
import {logout} from '../Actions/auth';
import BarIcon from '../../Assets/Images/BarIcon.png';
import OpenBar from '../../Assets/Images/OpenBar.png';



const NavBar = ({logout, isAuthenticated}) => {
    const [isOpen, setIsOpen] = useState(false);
    const handleClickOutSide = () => {setIsOpen(false)}
    const toggleMenu = () => {setIsOpen(!isOpen);}
    const navigate = useNavigate()

    const handleLogout = () => {
        return logout();
    }
    const handleLogin = () => {
        navigate('/Login')
    }
    
    
    const navLink  = [ 
    { 
    title: 'Contact Us',
    path: './Contact'
    },
    {  
    title: 'Donate',
    path: './Donate'
    },
    {
     title: 'About Us',
     path: './About'
    },

    
    {
        title:
         <>
            {
            isAuthenticated ? <span onClick={handleLogout}>Logout</span> :
                <span>Login</span>
            }
        </>,

      path: "./Login"
    },
    
]

    const handleClick = (path) => {
        navigate(`/${path}`)
        setIsOpen(false)
    }
    return(
        <nav>
        <section className="nav">
                        
                <Link to="./">
                    NFCS
                </Link>
                <ul>
                    {
                        navLink.map((nav, index) => {
                            return(
                                <>
                                <li key={index}>
                                   <Link to={nav.path}>{nav.title}</Link> 
                                </li>
                                
                                </>
                            )
                        })
                    }
                    
                </ul>
             <button style={{outline: 'none', border: 'none', background: 'none', margin: '.2rem 0 .2rem 0'}} 
             onClick={toggleMenu} className={`barIcon`}>
                {
                    !isOpen ?  <img src={BarIcon} width='70px' alt="" />
                    :
                    <img src={OpenBar} width='80px' alt="" />
                }
                   
             </button>
        </section>
    {
            isOpen && (
        <section className='dropdown'>
            
                {
                    navLink.map((nav, index) => {
                        return(
                        <span  key={index}>
                            <a onClick={ () => handleClick(nav.path)}>{nav.title}</a> 
                        </span>
                    )
                    }) 
                }
                
            
      
        </section> 
        )
 }
    {
        isOpen &&
            <section className='off' onClick={handleClickOutSide} >

            </section>
    }
        </nav>
    )
}

const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated
})

export default connect(mapStateToProps, {logout}) (NavBar);