import React from 'react'
import '../../Assets/CSS/Category.css';
import { categoryColors } from '../../Assets/JS/categoryColors';
import { Button } from '@mui/material';
import {Link} from 'react-router-dom'
const categ = [
    {
        categories: "NFCS",
        url: '/category/nfcs'
    },
    {
        categories: 'CATECHISM',
        url: '/category/catechism'
    },
    {
        categories: "ALUMNI",
        url: '/category/alumni'
    },
    {
        categories: "CHAPLAINCY",
        url: '/category/chaplaincy'

    },
    {
        categories: "BIBLE",
        url: '/category/bible'
    },
    
    {
        categories: "ACCOMMODATION",
        url: '/category/accomodation',
    },
    
    {
        categories: "SAINTS",
        url: '/category/saints',
    },
    {
        categories: "ORGANIZATION",
        url: '/category/organization'
    },
    {
        categories: "EUCHARIST",
        url: '/category/eucharist'
    },
    {
        categories: "SCHOOL",
        url: '/category/school',
    }
]
function CategoryHeader({category}) {
  return (
    <div className='Category container'>
        {
            categ.map((cat, index) => {
                return(
                    <span key={index} className='cat_span'>
                      <Button> <Link to={`${cat.url}`}>{cat.categories}</Link></Button>
                    </span>
                )
            })
        }

        <div className='line'></div>
    </div>
  )
}

export default CategoryHeader