import './App.css';
import '../src/Assets/CSS/Base.css';
import {Routes, Route, Switch} from 'react-router-dom';
import NavBar from '../src/Components/Navigation/NavBar.js';
import Feed from './Components/Body/Feed';
import Renderer from './Components/Renderer.js';
import Footer from './Components/Footer/Footer';

import {Provider} from 'react-redux';
import store from './Components/Body/store';
import Layout from './Components/HOC/Layout';
import PasswordResetRequest from './Components/Body/Requests/PasswordResetRequest';
import PasswordReset from './Components/Body/Requests/PasswordReset';
import Details from './Components/Body/moment/Details';
import Category from './Components/Body/Category';

function App() {
  return (
  <Provider store={store}>
      <div className="App">
        
       <Layout> 
        <Routes>
              <Route path="/" element={<Feed />} />
              <Route path="/:page" element={<Renderer />} />
              <Route element={() => 404} /> 

              {/** Password Resetting */}
              <Route path="/password/reset/request" element={<PasswordResetRequest />} />
              <Route path="/password/reset/confirm/:uid/:token" element={<PasswordReset />} />
              <Route path="/details/:slug/" element={<Details/>} />
              <Route path="/category/:category" element={<Category />} />
        </Routes>
        </Layout>
      </div>
  </Provider>
  );
}

export default App;
