import React, { useState } from 'react'
import '../../Assets/CSS/Donate.css';
import church from '../../Assets/Images/church.webp';
import { Box, Button, Modal } from '@mui/material';
import Transfar from './Transfar';
import ArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import ArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import CreditCardIcon from '@mui/icons-material/CreditCard';

function Donate() {

    const [open, setOpen] = useState(false)
    const [dropOpen, setDropOpen] = useState(false);

    const [transfar, setTransfar] = useState(false)
    const [card, setCard] = useState(false)
    const [payment, setPayment] = useState('')
    
    const option = [
       {
        payment: 'card',
       },
       {
        payment: 'transfar',
       }
    ]

    const style = {
        zIndex: '999999',
        padding: '1rem',
    }

    const handleOpen = () => {
        setOpen(!open)
    }

    const handleClose = () => {
        setOpen(false)
    }

    const handlePaymentOption = (option) => {
        if(option.payment === 'transfar'){
            setTransfar(true)
            setCard(false)
            setPayment('transfar')
            setDropOpen(!dropOpen)
        }
        else if(option.payment === 'card'){
            setCard(true)
            setTransfar(false)
            setPayment('card')
            setDropOpen(!dropOpen)
        }
    }

    const handlDropOpenClose = () => {
        setDropOpen(!dropOpen)
    }

  return (
    <div className="donate_container" >
        <div className='upper_header'>
            <div className='sub_header'>
                <span className='icon_circle'>
                  <img src="" alt="" />  
                </span>

                <span  className='icon_circle'>
                  <img src="" alt="" />  
                </span>
            </div>

            <article className='artiq'>
                <h2>
                  ST. Augustine Catholic chaplaincy, Institute of Management and Technology, Enugu Nigeria
                </h2> 
                
                <h3>
                    Help the chaplaincy
                </h3>
                   <p>
                    Our school chaplaincy program is dedicated to providing emotional support, spiritual guidance,
                 and pastoral care to students, staff, and families within our school community. Your donation can
                  help us continue to offer these valuable services and programs.
                
                By supporting our chaplaincy program, you're helping to create a nurturing and inclusive 
                environment where students feel safe, supported, and valued. Your contribution will enable us to
                 organize events, workshops, and retreats that promote personal growth, resilience, and compassion.
                Every donation, no matter the size, makes a difference. Your generosity allows us to continue
                 serving the diverse needs of our school community and fostering a culture of kindness and empathy.
                Thank you for partnering with us in our mission to make a positive difference in the lives of our students and their families." 
                </p>
            </article>
        </div>  

        <div className='lower'>
            <Button onClick={handleOpen}>Donate Now</Button>
            <Modal
            open={open}
            onClose={handleClose}
            aria-labelledby="parent-modal-title"
            aria-describedby="parent-modal-description"
            >
                <Box
                sx={{...style, width: 400}}
                >
                <span className='cancel' onClick={handleClose}><HighlightOffIcon /></span>
                <div className='pay_body'>
                <h2>Please fill the forms below</h2>
                <form>
                    <input type="text" name="first_name" placeholder='First name' />
                    <input type="text" name="last_name" placeholder='Last name' />
                    <input type="number" name='phone' placeholder='Phone number' />

                    <label htmlFor='remark'>Remark</label>
                    <textarea>
                    </textarea> 

                    <h3>
                        Payment method
                    </h3>

                    <div className='select'>
                        <div>
                            Select Payment
                        </div>
                        
                        <div className='icons' onClick={handlDropOpenClose}>
                            {
                                !dropOpen ?
                                 <ArrowDownIcon />
                                :
                                <ArrowUpIcon />
                            }
                        </div>
                    </div>
                    {
                        !dropOpen ? ''
                        :
                     <div>
                    {
                        option.map((opt, index) => {
                            return(
                                <span onClick={() => handlePaymentOption(opt)} className='drop_down'>
                                    {opt.payment}
                                </span>
                            )
                        })
                    }
                     </div>

                    }
                    
                    {
                        transfar ? 
                        (
                            <Transfar />
                        ):
                        (
                            card ? 
                            (
                                <p>Under construction...</p>
                            ) :

                            ''
                        ) 
                    }

                    <div className='submit_btn'>
                        <Button>Submit</Button>
                    </div>

                </form>
              </div>
                </Box>
            </Modal>
        </div>
    </div>
  )
}

export default Donate