import React, { useEffect, useState } from 'react'
import CategoryHeader from './CategoryHeader'
import '../../Assets/CSS/Category.css';
import image from  '../../Assets/Images/mario.jpg';
import { Link, useParams } from 'react-router-dom';
import axios from 'axios';
const BASE_URL = 'http://127.0.0.1:8000';

const Category = (props) => {
    const {category} = useParams()
    const [blogs, setBlogs] = useState([])
    const [loading, setLoading] = useState(false)

    const capitalizeFirstLetter = (letter) => {
        if (letter){
          return letter.charAt(0).toUpperCase() + letter.slice(1)
        }else{
          return '';
        }
      }

    useEffect(() => {
        setLoading(true)
        const fetchData = async () => {
            const config = {
                headers:{
                    'Content-Type': 'application/json'
                }
            }
            try{
                const res = await axios.post(`${BASE_URL}/blog/category/`, {category}, config)
                setBlogs(res?.data);
                setLoading(false)
            }catch(error){
                console.log(error)
                setLoading(false)
            }
        }

    fetchData()
    },[category])

    console.log(loading)
    const getCategoryBlogs = () => {

        try{
        let list = [];
         let result = [];
            blogs?.map((blog, index) => {
                return list.push(
                        <div key={index} className='catory_card'>
                            <div className='card_left'>
                                    <h2>{blog.category.toUpperCase()}</h2>
                                    <h3>{blog.title}</h3>
                                    <p>{blog.excerpt}</p>
                                </div>
                                <Link to={`/details/${blog.slug}/`}>
                                <div className='card_right'>
                                    <img src={blog.thumbnail1} width="200px" alt="category" />
                                </div>
                                </Link>
                    </div>
                )  
        })

    return list
        // for (let i = 0; i < list.length; i+=8){
        //     result.push(
        //         <div className=''>

        //             <div className=''>
        //                 {list[i]}
        //             </div>
        //         </div>
        //     )

        //     return result
        // }

    }catch(error){
        console.log(error)
    }

    }

  return (
   
    <div className='categ'>
        <CategoryHeader />
        <div className='categ_name'>
            {category ? category : ''}
        </div>
        <div className='flex_categ'>

        {
                    loading ? (
                        <div className='loading'>
                            <div className='loading_spin'>
                               loading....  
                            </div>
                            
                        </div>
                    ):(
                blogs.length <= 0 ? 
                            (
                                    <div className='error_width'>
                                    <div className='error_card'>
                                        Sorry! error fetching data! 
                                    </div>
                                    </div>
                            )
                                :
                            (
                                <>
                                
                                {
                                getCategoryBlogs()
                                }

                                </>
                            )
                    )
                 }
               
       </div>
     </div>
  )
}

export default Category