import React, { useEffect, useState } from 'react'
import Footer from '../../Footer/Footer'
import '../../../Assets/CSS/Details.css'
import featured from '../../Data/featured'
import { Button } from '@mui/material'
import { categoryColors } from '../../../Assets/JS/categoryColors'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios';
const oneImg = featured.slice(-3)[0]
console.log("===> ", oneImg)



function Details() {
  const {slug} = useParams()
const BASE_URL = 'http://127.0.0.1:8000';
  //States
  const [blogDetail, setBlogDetail] = useState([])

  useEffect(() => {
    axios.get(`${BASE_URL}/blog/${slug}/detail/`)
    .then(res => {
      setBlogDetail(res.data)
    })
    .catch((error) => {
      //Error handling
    })
  },[])

  const blogContent = () => {
    return {__html: blogDetail.content}
  }

  const capitalizeFirstLetter = (letter) => {
    if (letter){
      return letter.charAt(0).toUpperCase() + letter.slice(1)
    }else{
      return '';
    }
  }

const capitalizeAll = (letter) => {
      if(letter){
        return letter.toUpperCase()
      } else{
        return ''
      }
  }
  const imageBg = {backgroundImage: `url(${blogDetail.thumbnail1})`}

  return (
    <div className='detail container'>
      <div className='headerImage' style={imageBg}>
        <span>
          <h1 style={{ backgroundColor: categoryColors[capitalizeAll(blogDetail.category)]}}>{capitalizeAll(blogDetail.category)}</h1>
        </span>
        
        <h2>{capitalizeFirstLetter(blogDetail.title)}</h2>
        <span>{blogDetail.date_created}</span>
        <span className='fade_image'></span>
      </div>

        <section className='detail_body row'>
            <div className='article' dangerouslySetInnerHTML={blogContent()} />
            <div className='nav_btn'>
            <Button><Link to="/">Back to Blog</Link></Button>
            <span><Button><Link to=".">Back to Top</Link></Button></span>
            </div>

        </section>
    </div>
  )
}

export default Details