import React, { useState } from 'react'
import '../../Assets/CSS/Transfar.css';
import AssuredWorkloadIcon from '@mui/icons-material/AssuredWorkload';

function Transfar() {

    const bank = [
        {
            acct_name: 'st augustine catholic chaplaincy IMT Enugu',
            bank_name: 'First Bank plc',
            account: '00711564352'
        }
    ]

    const [copy, setCopy] = useState(false)

     const handleCopy = (contactCopy) => {
       navigator.clipboard.writeText(contactCopy);
       setCopy(true);
       setTimeout(()=> {
       setCopy(false)
       },2000);

    }

  return (
    <div className='transfar_container'>
       <span className="assureIcon">
        <AssuredWorkloadIcon />
       <h4>Nigeria Bank Transfar</h4>
       </span>

       <input type="number" name='phone' placeholder='Input Amount to transfer' />
       
       <div className='bank_details'>
        {
            bank.map((bnk, index) => {
                return(
                    <>
                    <span>Bank Name: <strong>{bnk.bank_name}</strong></span>
                    <span>account Name: <strong>{bnk.acct_name}</strong></span>
                    <span>Acctount Number: <strong>{bnk.account}</strong>  <b onClick={() => {handleCopy(bnk.account)}}>copy</b> </span>
                    {
                        copy ?
                        <span className='copy'>copied!</span>
                        :
                        ''
                    }
                    
                    </>
                )
            })
        }
       </div>
    </div>
  )
}

export default Transfar