import React, { useState} from 'react'
import '../../Assets/CSS/Login.css';
import { Button } from '@mui/material';
import { Link,  redirect, Redirect,  useNavigate} from 'react-router-dom';
import {connect} from 'react-redux';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import RemoveRedEyeOutlinedIcon from '@mui/icons-material/RemoveRedEyeOutlined';


import { login, reset_password } from '../Actions/auth';

function Login({login, isAuthenticated, errorMsg, isLoading}) { 
   const navigate = useNavigate()
   const [disable, setDisable] = useState(false)
   const [processing, setProcessing] = useState(false)
   const [connectionError, setConnectionError] = useState("")
   const [invalid, setInvalid] = useState("")
   const [showPassword, setShowPassword] = useState(false)


if(isAuthenticated === true){
  navigate('/')
}
  const [formData, setFormData] = useState({
    "email": "",
    "password": ""
  })

  const {email, password} = formData; // Destructure the state

  const handleChange = (e) => {
    setFormData({...formData, [e.target.name]:e.target.value}) 
    }


const toggleVisibility = () => {
  setShowPassword(!showPassword)
}

const handleSubmit = (e) => {
  
    e.preventDefault();
    // login(email, password) functonalities#
      setProcessing(true)

      login(email, password)
      setProcessing(false)
      if(isLoading && isAuthenticated){
        navigate('/')
      }

      setDisable(true)
        if(errorMsg.code === "ERR_NETWORK"){
      setConnectionError("Error! network connection refused ")
    } else if(errorMsg.code === "ERR_BAD_REQUEST"){
      setInvalid("Invalid email or password")
    }
    else{
      setProcessing(true)
      
    }
   }    



 console.log("How far ", errorMsg.code)
 console.log("Loading ? ", isLoading)

  return (
    <div className='AuthLogin'>
    <div className='Login'>
        <div className='login_card'>
                <h3>Welcome Back!</h3>
             <form onSubmit={e => handleSubmit(e)}>
                <span className='field__name'>Sign In</span>
                {
                  connectionError ? <p className="errorMsg"> {connectionError} </p> : ""
                }
                <section className='showPassword'>
                <input onChange={(e) => handleChange(e) } type='email' placeholder='Email' autoFocus={true} required
                  value={email}
                  name='email'
                />
                </section>
                <section className='showPassword'>
                    <input type={showPassword ? 'text' : 'password'} placeholder='Password'
                  onChange={(e) => handleChange(e)} value={password}
                  name='password'
                />
                {showPassword ?  <RemoveRedEyeOutlinedIcon onClick={toggleVisibility} /> :
                 <VisibilityOffIcon onClick={toggleVisibility} /> }
                </section>
              
                {invalid && !isLoading ? <span className='errorMsg'>{invalid}</span> : ""}

                <Button disable={disable} type="submit" className='login__button'>
                  {processing && !errorMsg ? "processing..." : "login" }</Button>
                  <p>Don't have NFCS acount yet? <Link to='/Register'>Register here</Link></p>
                <Link to="/password/reset/request">Forgotten Password? </Link>
            </form>
        </div>
      </div>
    </div>
    
  )
}
const mapStateToProps = state => ({
    isAuthenticated: state.auth.isAuthenticated,
    errorMsg: state.auth.errorMsg,
    isLoading: state.auth.isLoading,
  })
 
export default connect(mapStateToProps, {login}) (Login);

